
import React from "react";
import {Link } from "react-router-dom";
import "./FileCard.css";
import createIcon from "../icons/add_circle_FILL0_wght200_GRAD0_opsz40.svg";

// import axios from 'axios';
import CardBig from "./CardBig";
import CardSmall from "./CardSmall";
const Video= ({onchange,placeholder,name,value,baseUrl ,title,type}) =>{
    // drag drop file component

   const [dragActive, setDragActive] = React.useState(false);
   // ref
   const inputRef = React.useRef(null);
   
   // handle drag events
   const handleDrag = (e)=> {
     e.preventDefault();
     e.stopPropagation();
     if (e.type === "dragenter" || e.type === "dragover") {
       setDragActive(true);
     } else if (e.type === "dragleave") {
       setDragActive(false);
     }
   };
   
   // triggers when file is dropped
   const handleDrop = (e)=> {
     e.preventDefault();
     e.stopPropagation();
     setDragActive(false);
     if (e.dataTransfer.files && e.dataTransfer.files[0]) {
       // handleFiles(e.dataTransfer.files);
       console.log(e.dataTransfer.files)
     }
   };
   
   // triggers when file is selected with click
   const handleChange = (e)=> {
     e.preventDefault();
     if (e.target.files && e.target.files[0]) {
       // handleFiles(e.target.files);
      
     const formdata = new FormData()
     formdata.append("image",e.target.files[0])
    //  axios.post(`https://kc-uploads2.onrender.com/upload`,formdata)
    //  .then(res=>console.log(res))
    //  .catch(err=>console.log(err))
     }
   };
   
 // triggers the input when the button is clicked
   const onButtonClick = () => {
     inputRef.current.click();
   };
 
//  const [valueInput , setValueInput] = useState({val:value});
return(
   
    <div  className="popup-backgrop">

      <div  className="popup-card">
     
        <div  className="blank"> </div>
    <div  className="popup-outer">
 {name==="youtube"?
    <div  className="login-signup-left-signup">
      <h1 style={{"textAlign":"center"}}>
         Upload  Video To Youtube   </h1>
           <div  className="popup-input-card">
    <div  className="popup-inner"> 
 <form id="form-file-upload" onDragEnter={handleDrag} onSubmit={(e) => e.preventDefault()}>
      <input ref={inputRef} type="file" id="input-file-upload" multiple={true} onChange={handleChange} />
      <label id="label-file-upload" htmlFor="input-file-upload" className={dragActive ? "drag-active" : "" }>
        <div>
          <p>Drag and drop your Video here or</p>
          <button className="upload-button" onClick={onButtonClick}>
            Select an Video File From Drive</button>
        </div> 
      </label>
      { dragActive && <div id="drag-file-element" onDragEnter={handleDrag} onDragLeave={handleDrag} onDragOver={handleDrag} onDrop={handleDrop}></div> }
    </form>
    </div>
   <div  className="popup-button-card">
      <button  className="green-button"
       type="submit" id="signupBtn">Upload Video To Youtube</button>
    <p> youtube api to upload unlisted youtube video directly to Youtube Channel with customization </p>

  <Link to={baseUrl} className="black-button">
close
 </Link>
  
  </div>
    <Link to={`${baseUrl}/${name}`} className="white-button">
  Show All Videos
 </Link>
  </div>
   </div>
   :name==="google-drive"?<div  className="login-signup-left-signup">
   <h1 style={{"textAlign":"center"}}>
      Upload  Video To Google Drive   </h1>
      
 <div  className="popup-input-card">
 <div  className="popup-inner"> 
<form id="form-file-upload" onDragEnter={handleDrag} onSubmit={(e) => e.preventDefault()}>
   <input ref={inputRef} type="file" id="input-file-upload" multiple={true} onChange={handleChange} />
   <label id="label-file-upload" htmlFor="input-file-upload" className={dragActive ? "drag-active" : "" }>
     <div>
       <p>Drag and drop your Video here or</p>
       <button className="upload-button" onClick={onButtonClick}>
         Select an Video File From Drive</button>
     </div> 
   </label>
   { dragActive && <div id="drag-file-element" onDragEnter={handleDrag} onDragLeave={handleDrag} onDragOver={handleDrag} onDrop={handleDrop}></div> }
 </form>
 </div>
<div  className="popup-button-card">
   <button  className="green-button"
    type="submit" id="signupBtn">Upload Video To Google Drive</button>
<p> Free 15 GB Storage  ( Store Images only Or Videos if necessary )</p>
<Link to={baseUrl} className="black-button">
close
</Link>

</div>
 <Link to={`${baseUrl}/${name}`} className="white-button">
Show All Videos 
</Link>
</div>
</div>:"" }
<div class="input-card-outer grid-50">

<Link to={`${baseUrl}/featureVideoYoutube`}>
<CardSmall  alt="Youtube" title="Upload Video To Youtube "
 />
 </Link>
 
<Link to={`${baseUrl}/featureVideoGDrive`}>
<CardSmall alt="Google Drive" title="Upload Video To Google Drive "
/>
 </Link>
</div>
   </div>
   
	</div></div>

)

}

export default Video;

