import * as api from "../../api"; 


export const sendMyCourse  = (post) => async (dispatch)=>{
    try {
        const {data } = await  api.updateMyCourse(post)
        
        dispatch({
            type: "FETCH_MY_COURSE",
            
            payload:data
          });
        return data ;
    } catch (error) {
        console.log(error);
    }
}
