import React,{useState} from "react";

import {Link } from "react-router-dom";

const Calender = ({onchange,placeholder,name,value,baseUrl ,title,disable})=>{ 
     const date = value?.slice(0, 10);
  const [valueInput , setValueInput] = useState({val:date});

 
 // Get the current date
 const today = new Date();
 const year = today.getFullYear();
 const month = String(today.getMonth() + 1).padStart(2, '0');
 const day = String(today.getDate()).padStart(2, '0');
 const minDate = `${year}-${month}-${day}`;

     return(<>
      <div  className="popup-backgrop">
      <div  className="popup-card">
        <div  className="blank"> </div>
    <div  className="popup-outer">
 <div  className="popup-inner"> 
 
 
    <div  className="login-signup-left-signup"><h1 style={{"textAlign":"center"}}>Update {title} </h1>
    <div  className="popup-input-card">
    {disable==="future"? <input type="date"  max={minDate} placeholder={placeholder} className="input-textbox" title={title} value={valueInput.val} name={name}  
     onChange={e=>{
        setValueInput({...valueInput,val:e.target.value});
       
        
       }
       
    }
    onChangeCapture={onchange }
    />:disable==="past"?<input type="date"  min={minDate} placeholder={placeholder} className="input-textbox" title={title} value={valueInput.val} name={name}  
    onChange={e=>{
       setValueInput({...valueInput,val:e.target.value});
      
       
      }
      
   }
   onChangeCapture={onchange }
   />:<input type="date"   placeholder={placeholder} className="input-textbox" title={title} value={valueInput.val} name={name}  
   onChange={e=>{
      setValueInput({...valueInput,val:e.target.value});
     
      
     }
     
  }
  onChangeCapture={onchange }
  />}
    </div>
    <div  className="popup-button-card">
      <button  className="green-button" type="submit" id="signupBtn">{value?"Update":"Add"}</button>
  
  <Link to={baseUrl} className="black-button">
close
 </Link>
  
  </div>
   
  </div>
   </div>
   
   </div>
	    
	</div></div>                              
     </>)  
   }
   export default Calender;